import { Button } from '@material-ui/core';
import { useData } from 'contexts/Data';
import React from 'react';
import { Subject } from 'typings/Subject';
import { areSubjects } from 'typings/TypeGuards';
import { useWorker } from 'contexts/Worker';

export default function () {
  const { setSubjects } = useData();

  const { getTeachers } = useWorker();

  const { setAllTeachers } = useData();

  function handleFile(event: ProgressEvent<FileReader>) {
    if (event.target?.result) {
      const subjects: Subject[] = JSON.parse(event.target.result as string);

      if (areSubjects(subjects)) {
        console.log(subjects);
        setSubjects(subjects);
        getTeachers(subjects).then(setAllTeachers);
      } else {
        console.log('Invalid file data.');
      }
    }
  }

  function handleChange(event: React.ChangeEvent<HTMLInputElement>) {
    if (!event.target.files) return;

    const file: File = event.target.files[0];

    if (!file.type.endsWith('json')) return;

    const reader = new FileReader();

    reader.onload = handleFile;

    reader.readAsText(file);

    // Reset file path, same file can fire event multiple times
    event.target.value = '';
  }

  return (
    <Button component="label" variant="outlined">
      Input subjects
      <input
        hidden
        accept=".json"
        type="file"
        value={undefined}
        onChange={handleChange}
      />
    </Button>
  );
}
