import { wrap } from 'comlink';
import React, { useContext } from 'react';
// eslint-disable-next-line import/no-webpack-loader-syntax
import Worker from 'worker-loader!./myWorker.worker';
import { WorkerApi } from './myWorker.worker';

const workerApi = wrap<WorkerApi>(new Worker());

const WorkerContext = React.createContext(workerApi);

export const useWorker = () => useContext(WorkerContext);
