import { Button, CircularProgress } from '@material-ui/core';
import { useData } from 'contexts/Data';
import React, { useState } from 'react';
import styled from 'styled-components';
import { useWorker } from 'contexts/Worker';

const LoadingWrapper = styled.div`
  position: relative;
`;

const LoadingIcon = styled(CircularProgress).attrs(props => ({
  halfsize: (props.size as number) / 2,
}))`
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -${props => props.halfsize}px;
  margin-left: -${props => props.halfsize}px;
`;

type Props = {
  closeDialog: () => void;
};

export default function ({ closeDialog }: Props) {
  const { buildTimetables } = useWorker();

  const {
    subjects,
    selectedTeachers,
    week,
    maxBreaks,
    maxPeriods,
    minPeriods,
    setTimetables,
  } = useData();

  const [loading, setLoading] = useState(false);

  async function handleSubmit() {
    setLoading(true);

    const timetables = await buildTimetables(
      subjects,
      selectedTeachers,
      week,
      maxBreaks,
      maxPeriods,
      minPeriods
    );

    console.log('Timetables:', timetables);

    setLoading(false);

    setTimetables(timetables);

    closeDialog();
  }

  return (
    <LoadingWrapper>
      <Button color="primary" disabled={loading} onClick={handleSubmit}>
        Build timetables
      </Button>
      {loading && <LoadingIcon size={24} />}
    </LoadingWrapper>
  );
}
